import { DimensionDefEntity, DimensionType, DimensionInputType } from './dimensiondef.entity';
import { Guid } from './guid.util';

class DimenDefEntityUtil {
    constructor() {
    }
    static newDimendefEntity(flowFormId: string): DimensionDefEntity {
        const newDimendef = {
            id: '',
            code: '',
            entryFormID: '',
            dimensionType: DimensionType.BusinessField,
            dimensionCode: '',
            dimensionName: '',
            inputType: DimensionInputType.Help,
            enumList: null,
            smartHelp: '',
            multiSelect: true,
            helpFilter: '',
            isUse: true,
            devLayer: '',
            contentInfo: null,
            sortOrder: 0,
            sysInit: 0,
            remarks: ''
        };
        newDimendef.id = Guid.newGuid();
        newDimendef.entryFormID = flowFormId;
        return newDimendef;
    }
}
export { DimenDefEntityUtil };
