import { HttpService } from '@ecp-caf/caf-common';
import { Injectable } from '@angular/core';
import { DimensionDefEntity } from '../entity/dimensiondef.entity';
import { Observable } from 'rxjs';

@Injectable()
export class WFDimensionDefViewmodel { 
    private url = '/api/runtime/wf/v1.0/dimensionDef';
    constructor(
        private http: HttpService
    ) { }

    loadDimensionDefs(flowFormId: string): Observable<any> { 
        if (flowFormId) { 
            const url = this.url + `?entryFormID=${flowFormId}`;
            return this.http.get(url);
        }
    }
    removeDimensionDef(id: string): Observable<any> { 
        if (id) {
            const url = this.url + `/${id}`;
            return this.http.delete(url, {});
        }
    }

    addDimensionDef(dimensionDef: DimensionDefEntity): Observable<any> { 
        return this.http.post(this.url, dimensionDef);
    }

    updateDimensionDef(dimensionDef: DimensionDefEntity): Observable<any> {
        return this.http.patch(this.url, dimensionDef);
    }


    getDimensionDefSet(flowFormId: string) {
        const url = `/api/runtime/wf/v1.0/dimensionDef/customSetting?entryFormId=${flowFormId}`;
        return this.http.get(url);
    }
}
