import { Component, OnInit, OnChanges, SimpleChanges, Input } from '@angular/core';
import { MessagerService } from '@farris/ui-messager';
import { DimenDefEntityUtil } from './entity/dimendef-entity.util';
import { DimensionDefEntity, ItemEditState, DimensionInputType } from './entity/dimensiondef.entity';
import { WFDimensionDefViewmodel } from './service/wf-dimension-def.viewModel';
import { NotifyService } from '@farris/ui-notify';
import { WFDimensionDefUIState } from './service/wf-dimension-def.uistate';
import { forkJoin } from 'rxjs/observable/forkJoin';
import { switchMap, catchError } from 'rxjs/operators';
import { of, Observable } from 'rxjs';

@Component({
  selector: 'wf-dimension-def',
  templateUrl: './wf-dimension-def.component.html',
  styleUrls: ['./wf-dimension-def.component.css'],
})
export class WfDimensionDefComponent implements OnInit, OnChanges {

  private _flowFormId: string;

  @Input()
  get flowFormId() {
    return this._flowFormId;
  }
  set flowFormId(id: string) {
    this._flowFormId = id;
    this.load();
    this.getDimensionDefSet(this._flowFormId);
  }

  @Input()
  set schema(s: any) {
    this.uistate.schema = s;
  }
  get schema() {
    return this.uistate.schema;
  }

  @Input()
  set beMetaData(m: any) {
    this.uistate.beMetaData = m;
  }
  get beMetaData() {
    return this.uistate.beMetaData;
  }

  @Input()
  entry: 'flowForm' | 'billCategory' = 'flowForm';

  // tslint:disable-next-line: variable-name
  _editable = false;
  @Input()
  set editable(e: boolean) {
    this._editable = e;
    for (const d of this.editData) {
      d.canEdit = this._editable;
    }
  }
  get editable() {
    return this._editable;
  }

  private data = new Array<DimensionDefEntity>();
  editData = new Array<ItemEditState>();

  dimensionDefSet: any;

  constructor(
    private messageService: MessagerService,
    private viewmodel: WFDimensionDefViewmodel,
    private notifyService: NotifyService,
    private uistate: WFDimensionDefUIState
  ) { }

  ngOnInit() {
  }
  ngOnChanges(changes: SimpleChanges) {

  }

  load() {
    if (this.flowFormId) {
      this.viewmodel.loadDimensionDefs(this.flowFormId).subscribe((re: Array<DimensionDefEntity>) => {
        this.data = re;
        this.editData = [];
        if (this.data && this.data.length > 0) {
          this.data.forEach(item => {
            this.addItem(item, this.editable);
          });
        }
      });
    }
  }

  getDimensionDefSet(flowFormId: string) {
    if (flowFormId) {
      this.viewmodel.getDimensionDefSet(flowFormId).subscribe(re => {
          this.dimensionDefSet = re;
      });
    }
  }

  hasItem(index: number) {
    return this.editData.find(item => item.index === index);
  }

  getItem(index: number) {
    return this.hasItem(index) ? this.editData.find(item => item.index === index).itemData : null;
  }


  setItem(index: number, itemData: any) {
    this.editData.find(item => item.index === index).itemData = itemData;
  }

  addItem(itemData: DimensionDefEntity, canEdit: boolean) {
    const editDataItem = new ItemEditState();
    editDataItem.canEdit = canEdit;
    editDataItem.index = this.editData.length;
    editDataItem.itemData = Object.assign({}, itemData);
    editDataItem.id = itemData.id;
    this.editData.push(editDataItem);
  }

  getTitle(index: number) {
    let suffix = '';
    switch (index) {
      case 0:
        suffix = 'ä¸';
        break;
      case 1:
        suffix = 'äº';
        break;
    }
    return `ç»´åº¦${suffix}`;
  }

  getState(index: number): boolean {
    if (!this.hasItem(index)) {
      return false;
    }
    const sysInit = this.editData.find(item => item.index === index).itemData.sysInit;
    return sysInit === 1 ? true : false;
  }

  isEditState(index: number): boolean {
    if (!this.hasItem(index)) {
      return false;
    }
    return this.editData.find(item => item.index === index).canEdit;
  }

  edit(item: ItemEditState) {
    this.editData.find(d => d.id === item.id).canEdit = true;
  }

  remove(item: ItemEditState) {
    if (this.dimensionDefSet && (item.id === this.dimensionDefSet.dimension1 || item.id === this.dimensionDefSet.dimension2)) {
      this.messageService.info('å½åç»´åº¦å®ä¹å·²å¨æµç¨åéç»´åº¦éç½®ä¸­å¯ç¨');
      return;
    }
    if (this.entry === 'flowForm') {
      this.removeDimensionDef(item);
      // const i = this.data.findIndex(d => d.id === item.id);
      // if (i > -1) {
      //   this.data.splice(i, 1);
      // }
    } else {
      this.messageService.question(`ç¡®è®¤å é¤'${this.getTitle(item.index)}'åï¼`, () => {
        if (this.data.findIndex(d => d.id === item.id) === -1) {
          this.removeDimensionDef(item);
          this.notifyService.success('å é¤æå');
          return;
        }
        this.viewmodel.removeDimensionDef(item.id).subscribe(re => {
          this.removeDimensionDef(item);
          const i = this.data.findIndex(d => d.id === item.id);
          if (i > -1) {
            this.data.splice(i, 1);
          }
          this.notifyService.success('å é¤æå');
        }, err => {
          this.notifyService.error('å é¤å¤±è´¥ï¼è¯·èç³»ç®¡çåæ¥çåå ï¼');
        });
      });
    }
  }

  getDimensionDefs() {
    return this.editData.map(d => d.itemData);
  }

  private removeDimensionDef(item: ItemEditState) {
    const index = this.editData.findIndex(d => d.id === item.id);
    if (index > -1) {
      this.editData.splice(index, 1);
    }
    if (index < this.editData.length) {
      for (let i = 0; i < this.editData.length; i++) {
        this.editData[i].index = i;
      }
    }
  }

  save(item: ItemEditState) {
    const v = this.validate(item.itemData);
    if (!v.result) {
      this.messageService.info(v.message);
      return;
    }
    if (this.data.findIndex(d => d.id === item.id) === -1) {
      this.viewmodel.addDimensionDef(item.itemData).subscribe(re => {
        this.endEdit(item);
        this.data.push(Object.assign({}, item.itemData));
        this.notifyService.success('ä¿å­æå');
      }, err => {
        this.notifyService.error('ä¿å­å¤±è´¥ï¼è¯·èç³»ç®¡çåæ¥çåå ï¼');
      });
    } else {
      this.viewmodel.updateDimensionDef(item.itemData).subscribe(re => {
        this.endEdit(item);
        const data = this.data.find(d => d.id === item.id);
        Object.assign(data, item.itemData);
        this.notifyService.success('ä¿å­æå');
      }, err => {
        this.notifyService.error('ä¿å­å¤±è´¥ï¼è¯·èç³»ç®¡çåæ¥çåå ï¼');
      });
    }
  }

  saveAll(): Observable<boolean> {
    const deleteDefs = this.data.filter(d => this.editData.findIndex(e => e.id === d.id) === -1);
    const addDefs = this.editData.filter(e => this.data.findIndex(d => d.id === e.id) === -1).map(e => e.itemData);
    const updateDefs = this.editData.filter(e => this.data.findIndex(d => d.id === e.id) > -1)
      .map(e => e.itemData)
      .filter(d => d.sysInit === 0);
    return forkJoin(deleteDefs.map(d => {
      return this.viewmodel.removeDimensionDef(d.id);
    }).concat(updateDefs.map(u => {
      return this.viewmodel.updateDimensionDef(u);
    }).concat(addDefs.map(a => {
      return this.viewmodel.addDimensionDef(a);
    })))).pipe(
      switchMap(re => {
        this.data = this.editData.map(e => e.itemData);
        // this.editData.map(e => e.canEdit = false);
        return of(true);
      }),
      catchError(err => {
        console.error(err);
        return of(false);
      })
    );
    //   .subscribe(re => {
    //   this.data = this.editData.map(e => e.itemData);
    //   this.editData.map(e => e.canEdit = false);
    // },
    // err => {
    //   this.notifyService.error('ä¿å­ç»´åº¦å®ä¹å¤±è´¥ï¼è¯·èç³»ç³»ç»ç®¡çåæ¥çåå ï¼');
    // });
  }

  cancel(item: ItemEditState) {
    this.endEdit(item);
    if (this.data.findIndex(d => d.id === item.id) === -1) {
      this.removeDimensionDef(item);
      return;
    } else {
      Object.assign(this.editData.find(d =>
        d.id === item.id).itemData, this.data.find(d => d.id === item.id));
    }
  }

  addHandler() {
    if (this.entry === 'flowForm' && !this.editable) {
      return;
    }
    const info = DimenDefEntityUtil.newDimendefEntity(this.flowFormId);
    this.addItem(info, true);
  }

  isOtherItemEditting(index: number): boolean {
    let isOtherItemEditting = false;
    this.editData.forEach(item => {
      if (item.canEdit === true && item.index !== index) {
        isOtherItemEditting = true;
        this.messageService.info(`è¯·åä¿å­å¯¹äº'${this.getTitle(item.index)}'çä¿®æ¹ã`);
      }
    });
    return isOtherItemEditting;
  }

  endEdit(item: ItemEditState) {
    for (const d of this.editData) {
      if (d.id === item.id) {
        d.canEdit = false;
      }
    }
  }
  private validate(d: DimensionDefEntity) {
    if (!d.code) {
      return { result: false, message: 'ç»´åº¦å®ä¹ä¸åè®¸ç¼å·ä¸ºç©ºï¼è¯·ä¿®æ¹ï¼' };
    } else if (!d.dimensionName) {
      return { result: false, message: 'ç»´åº¦å®ä¹ä¸åè®¸ç»´åº¦åç§°ä¸ºç©ºï¼è¯·ä¿®æ¹ï¼' };
    } else if (!d.dimensionCode) {
      return { result: false, message: 'ç»´åº¦å®ä¹ä¸åè®¸åéç»´åº¦ä¸ºç©ºï¼è¯·ä¿®æ¹ï¼' };
    } else if (d.inputType === DimensionInputType.Help && !d.smartHelp) {
      return { result: false, message: 'ç»´åº¦å®ä¹æ°æ®ç±»åä¸ºå¸®å©ï¼ä½å¸®å©å¼ä¸ºç©ºï¼è¯·ä¿®æ¹ï¼' };
    } else if (d.inputType === DimensionInputType.Enum && !d.enumList) {
      return { result: false, message: 'ç»´åº¦å®ä¹æ°æ®ç±»åä¸ºæä¸¾ï¼ä½æä¸¾å¼ä¸ºç©ºï¼è¯·ä¿®æ¹ï¼' };
    }
    return { result: true, message: '' };
  }
}
