import { Component, OnInit, ViewChild, Input, OnChanges, SimpleChanges } from '@angular/core';
import { FarrisPageDetailComponent } from '@farris/ui-wizard';
import { GSPCommonObject } from '@gsp-bef/gsp-cm-metadata';

@Component({
  selector: 'select-object',
  templateUrl: './select-object.component.html',
  styleUrls: ['./select-object.component.css']
})
export class SelectObjectComponent implements OnInit, OnChanges {
  // /**
  //   * è¾å¥åæ°æ ¼å¼
  //   */
  // data = {
  //   be: null,              // beå®ä½
  //   selectedObj: null,     // beèç¹
  //   BeFields: [],          // Beå­æ®µåè¡¨
  //   selectedBeFields: [],  // Beå­æ®µIDåè¡¨
  // };

  @Input() data;

  enableNextStep = true;
  private detailPage: FarrisPageDetailComponent = null;
  @ViewChild(FarrisPageDetailComponent) set cmpPageDetail(
    cmp: FarrisPageDetailComponent
  ) {
    this.detailPage = cmp;
  }

  // è®°å½å½åBEææè¡¨çcode
  treeTables = [];
  grid_selected = {};
  readonlyFields = []; // ä¸å¯åæ¶å¾éçå­æ®µ

  /** å·¦ä¾§æ ä¿¡æ¯ */
  treeData: any[] = [];
  farrisTreeData = [];

  treeConfig = {
    idField: 'ID',
    columns: [
      { field: 'Name', title: 'Name', width: 200 }
    ],
    /** å¶å­èç¹å¾æ  */
    leafIcon: 'f-icon f-icon-folder text-info',
    /** èç¹å±å¼å¾æ  */
    expandIcon: 'f-icon f-icon-folder-open text-info',
    /** èç¹æå å¾æ  */
    collapseIcon: 'f-icon f-icon-folder text-info',
    /**  æ åè¡¨é»è®¤å±å¼å±çº§ã -1ï¼ ä¸å±å¼ï¼ 0ï¼ å¨é¨å±å¼ï¼ >0:  å±å¼å°æå®çº§æ°ï¼ */
    expandLevel: 2,
  };

  constructor() { }

  ngOnInit() {
    this.load();
  }
  ngOnChanges(changes: SimpleChanges): void {
    this.load();
  }

  load() {
    if (this.data && this.data.be) {
      const mainObject = this.data.be.MainObject;
      this.treeData = this.getTablesTree(mainObject);
      this.farrisTreeData = this.getFarrisTreeData(this.treeData);
    }
  }
  /**
   * ç»è£farris-treeå·¦ä¾§æ 
   */
  getFarrisTreeData(data: any[]) {
    const newArr = [];
    if (data && data.length) {
      data.forEach(item => {
        let childrenArr = [];
        if (item && item.Children && item.Children.length) {
          childrenArr = this.getFarrisTreeData(item.Children);
        }
        newArr.push({ data: item, children: childrenArr, expanded: true });
      });
    }
    return newArr;
  }
  /**
   * ç»è£å·¦ä¾§æ 
   */
  getTablesTree(mainObject: GSPCommonObject) {
    const { ContainChildObjects, ColumnGenerateID, Keys, ...parentTable } = mainObject;
    this.treeTables.push(parentTable.Code);

    // const selectedAll = this.grid_selected[parentTable.Code] ? false : true;
    // this.gridData[parentTable.Code] = this.getGridFields(parentTable.ContainElements, parentTable.Code, selectedAll);

    if (ContainChildObjects && ContainChildObjects.length > 0) {
      parentTable['Children'] = [];
      ContainChildObjects.forEach(child => {
        const childTree = this.getTablesTree(child as GSPCommonObject);
        parentTable['Children'] = parentTable['Children'].concat(childTree);
      });
    }
    // å¤æ­ä¸»é®
    if (ColumnGenerateID) {
      this.readonlyFields.push(ColumnGenerateID.ElementID);
    }
    // å¤æ­ä¸»ä»è¡¨çå³èå­æ®µ
    if (Keys && Keys.length > 0) {
      Keys.forEach(key => {
        this.readonlyFields.push(key.SourceElement);
      });

    }
    return [parentTable];
  }

  /**
   * éä¸­èç¹äºä»¶
   * @param event è¢«éä¸­èç¹
   */
  handleTreeSelection(event): void {
    this.data.selectedObj = event.node.data;
  }

  nextStep() {
    // è®¾ç½®å½åé¡µä¼ éåºå»æ°æ®
    this.data.BeFields = this.data.selectedObj.ContainElements;
    this.detailPage.setPageData(this.data.selectedObj);
    // ä¸ä¸æ­¥
    this.detailPage.nextStep();
  }
  cancel() {
    this.detailPage.cancelWizard();
  }
  pagedetailSelected(args) {

  }
}
