import { GSPCommonModel, GSPCommonElement, GSPElementObjectType } from '@gsp-bef/gsp-cm-metadata';
import { SelectElementComponent } from '../refModule/select-element/select-element.component';
import { BsModalService } from '@farris/ui-modal';

@Component({
  selector: 'app-enum-edit',
  templateUrl: './enum-edit.component.html',
  styleUrls: ['./enum-edit.component.css']
})
export class EnumEditComponent implements OnInit {
  @HostBinding('class')
  cls = 'farris-main-area flex-column';

  columns = [
    { field: 'key', width: 100, title: 'é®', editor: { type: EditorTypes.TEXTBOX, options: {} } },
    { field: 'value', width: 100, title: 'å¼', editor: { type: EditorTypes.TEXTBOX, options: {} } }];
  @ViewChild('dg') dg: DatagridComponent;
  @ViewChild('buttons') btnRef: TemplateRef<any>;

  confirm = new Subject<any>();
  closeModalEmitter = new Subject<any>();

  /**
   * æ¾ç¤ºå¼
   */
  editData = new Array();

  maxNum = 0;

  /**
   * å®éå¼
   */
  get enumValues(): EnumItem[] {
    const arr = [];
    if (this.editData && this.editData.length >= 0) {
      this.editData.forEach(item => {
        const enumItem = new EnumItem();
        enumItem.key = item.key;
        enumItem.value = item.value;
        arr.push(enumItem);
      });
    }
    return arr;
  }

  constructor(
    private messageService: MessagerService,
    private uistate: WFDimensionDefUIState,
    private modalService: BsModalService,
    private resolver: ComponentFactoryResolver,
    private injector: Injector,
  ) { }

  ngOnInit() {

  }

  /**
   * å è½½æ¾ç¤ºæ°æ®
   */
  load(enumValues) {
    this.editData = [...this.createData(enumValues)];
  }

  /**
   * ç¡®è®¤
   */
  onConfirm(): void {
    this.closeCell();
    if (this.checkBeforeSave()) {
      this.confirm.next(this.enumValues);
      this.closeModalEmitter.next();
    }
  }

  /**
   * åæ¶
   */
  cancel(): void {
    this.closeModalEmitter.next();
  }

  /**
   * ä¿å­åæ£æ¥
   */
  checkBeforeSave(): boolean {
    // â  ç©ºï¼åéè¿ï¼
    if (!(this.enumValues) || this.enumValues.length === 0) {
      this.messageService.warning('è¯·æ·»å æä¸¾å¼ã');
      return false;
    }
    // â¡ éç©ºï¼åæ ¡éªæ¯ä¸ªçé®å¼æ¯å¦ä¸ºç©ºï¼
    for (const item of this.enumValues) {
      if (!item.key || !item.value) {
        this.messageService.warning('æä¸¾ç[é®][å¼]åä¸åè®¸ä¸ºç©ºã');
        return false;
      }
    }
    // â¢ å¤äºä¸ä¸ªï¼ç´¢å¼ä¸åè®¸éå¤ï¼
    if (this.enumValues.length > 1) {
      for (let i = 0; i < this.enumValues.length; i++) {
        const code1 = this.enumValues[i].value;
        const name1 = this.enumValues[i].key;
        for (let j = i + 1; j < this.enumValues.length; j++) {
          const code2 = this.enumValues[j].value;
          const name2 = this.enumValues[j].key;

          if (code1 === code2) {
            this.messageService.warning(`æä¸¾ç[ç¼å·]ä¸åè®¸éå¤ï¼éå¤ç¼å·ä¸º[${code1}]ã`);
            return false;
          }
          if (name1 === name2) {
            this.messageService.warning(`æä¸¾ç[åç§°]ä¸åè®¸éå¤ï¼éå¤åç§°ä¸º[${name1}]ã`);
            return false;
          }
        }
      }
    }
    return true;
  }

  /**
   * åæ¶ç¼è¾
   */
  closeCell() {
    this.dg.endCellEdit();
  }
  /**
   * å¼å¥
   */
  import() {
    if (!this.uistate.beMetaData) {
      this.messageService.error('æªè·åå°BEåæ°æ®ï¼è¯·èç³»ç®¡çåæ¥çåå ï¼');
      return;
    }
    const model: GSPCommonModel = this.uistate.beMetaData.content;
    const compFactory = this.resolver.resolveComponentFactory(SelectElementComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.selectEleInfo.be = model;
    compRef.instance.selectEleInfo.singleElement = true;
    compRef.instance.selectEleInfo.assoRefElement = false;
    compRef.instance.confirm.subscribe(data => {
      const selectedElement = data.selectedBeFields[0] as GSPCommonElement;
      if (selectedElement.ObjectType === GSPElementObjectType.Enum) {
        this.editData = new Array();
        let k = 0;
        for (const enumValue of selectedElement.ContainEnumValues) {
          this.editData.push({
            id: k,
            key: enumValue.Value,
            value: enumValue.Name,
          });
          k++;
        }
        this.maxNum = k;
      } else {
        this.messageService.info('éä¸­å­æ®µå¹¶éæä¸¾å­æ®µï¼è¯·äºè§£ï¼');
      }
    });
    const modalConfig = {
      title: 'æä¸¾å­æ®µéæ©',
      width: 1000,
      height: 550,
      showButtons: false
    };
    const dialog = this.modalService.show(compRef, modalConfig);
    compRef.instance.closeModal.subscribe(() => {
      dialog.close();
    });
  }

  /**
   * æ°å¢
   */
  add() {
    this.closeCell();
    const newId = this.maxNum;
    this.editData = this.editData.concat([{
      id: newId
    }]);
    this.maxNum++;
  }

  /**
   * å é¤
   */
  remove() {
    this.closeCell();
    if (this.dg.selectedRow) {
      const id = this.dg.selectedRow.id;
      this.dg.selectPrevRow();
      this.editData = this.editData.filter(n => n.id !== id);
    }
  }

  createData(enumValues: EnumItem[]) {
    const arr = [];
    let k = 0;
    for (const enumValue of enumValues) {
      arr.push({
        id: k,
        key: enumValue.key,
        value: enumValue.value,
      });
      k++;
    }
    this.maxNum = k;
    return arr;
  }
}
