  enumListText = () => {
    if (!this.getIsEnumInput()) {
      return '';
    }
    let display = '';
    if (this.data.enumList) {
      const enumList = JSON.parse(this.data.enumList);
      if (enumList.length > 0) {
        enumList.forEach(item => {
          display = display.concat(`${item.key}:${item.value}; `);
        });
      }
    }
    return display;
  }

