import {
    Component, Input, AfterViewInit,
    ViewEncapsulation, Output, EventEmitter, ElementRef, Renderer2, OnInit, ViewChild
} from '@angular/core';
@Component({
    selector: 'wf-dimension-panel',
    templateUrl: './panel-item-list.component.html',
    styleUrls: ['./panel-item-list.component.css'],
    encapsulation: ViewEncapsulation.None
})

export class WFDimensionPanelComponent implements AfterViewInit, OnInit {

    @ViewChild('farrisPanel') farrisPanel: ElementRef;

    /* è®¾ç½®å®½é« */
    @Input() width: number;
    @Input() height: number;



    /* èªå®ä¹className */
    @Input() className = [];

    /* é¢æ¿æ¯å¦å¯æ¶æ  é»è®¤æ¶æ*/
    @Input() foldable = true;

    /* é¢æ¿æ¯å¦å¨é¨å±å¼  é»è®¤ä¸æ¯å¨é¨å±å¼*/
    @Input() defaultExpand: boolean;

    // è®¾ç½®æé£ç´æ¨¡å¼  è³å¤åªæä¸ä¸ªpanelå¯ä»¥è¢«éä¸­
    @Input() accordion = false;

    /* panelæ°æ®å­å¨ */
    @Input() get model() {
        return this.modelValue;
    }

    set model(modelValue) {
        // è·åç¨æ·è¾å¥çå¼
        this.modelValue = modelValue;
        this.updateSubscriber();
    }


    @Output() modelChange: EventEmitter<Array<string | number>> = new EventEmitter<Array<string | number>>();

    // @HostBinding('class.farris-panel') isPanel =

    // ååç»å®  å½modelæ°ç»æ¹åæ¶ï¼ç¸åºçactiveç¶æçpanelä¹è¦æ¹å ä»èæ¹åè§å¾
    // ååç»å® ç¨äºå­åactiveç¶æçpanel
    modelValue: Array<string | number> = [];
    subscriber: Array<() => void> = [];

    constructor(
        private render: Renderer2) {
    }

    ngOnInit() {
    }

    ngAfterViewInit(): void {
        // æ·»å classå
        if (this.className.length) {
            this.className.forEach(ele => {
                this.render.addClass(this.farrisPanel.nativeElement, ele);
            });
        }
        this.updateSubscriber();
    }

    /**
     * @param value panelå­å¨æ¿æ´»å¼
     */
    updateModel(value: string) {
        const index = this.modelValue.findIndex(val => val === value);
        // å¦ææ²¡æå¹éå°ç¹å»çpanelå¼  å¯è½modeléé¢ä¸å­å¨ å¯è½modelä¸ºç©º
        if (index < 0) {
            // è¥æ¯æé£ç´æ¨¡å¼  åæææpanelå¤äºæªæ¿æ´»ç¶æ
            if (this.accordion) {
                this.model = [];
            }
            // å¦æä¸æ¯æé£ç´æ¨¡å¼  ç´æ¥æ¿æ´»å³å¯
            this.modelValue.push(value);
            this.updateSubscriber();
            return this.modelChange.emit(this.modelValue);
        }
        // å¦æindexä¸ä¸º-1
        // æé£ç´æ¨¡å¼  å³é­æææ¿æ´»ç¶æ
        if (this.accordion) {
            this.model = [];
        } else {
            // éæé£ç´æ¨¡å¼ ç´æ¥å³é­å½åpanelå³å¯
            this.modelValue.splice(index, 1);
        }
        this.updateSubscriber();
        this.modelChange.emit(this.modelValue);
    }

    /* å½æ°åå */
    private updateSubscriber(): void {
        this.subscriber.forEach(handle => handle());
    }
}
